
.App {
  margin: 0;
  width: 100%;
  min-height: 100vh;
  background-image: url('../Images/sky.jpg');
  background-position: cover;
  opacity: 0.8;
}
.Login-Form_Wrapper{
  display: flex;
  font-family: "Press Start 2P",sans-serif;
  align-items: center;
  justify-content: center;
  min-height: 520px;
  margin: 0;
}
.Login-Img{
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
}
.Login-Form {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-image: url("../Images/filling.jpg"); */
  flex-direction: column;
  border-top-right-radius: 1%;
  text-align: center;
  width: 600px;
  height: 300px;
  align-items: center;
  gap: 10px;
  z-index: 2000;
  background-size: cover; /* Add this line to scale the background image */
  border: 8px solid #f63630;
}

.Login-Form #Login-UserName{
  width: 350px;
  min-height: 30px;
  font-family: "Press Start 2P",sans-serif;
  text-transform: uppercase;
  text-align: center;
  border: none;
  background-color: white;

}
.Login-Form #Login-PassWord{
  width: 350px;
  min-height: 30px;
  font-family: "Press Start 2P",sans-serif;
  text-transform: uppercase;
  text-align: center;
  border: none;
  background-color: white;

}
.Login-Form button{
  background-color: transparent;
  padding:10px;  
  line-height: 25px;
  border:none ;
  font-family: "Press Start 2P",sans-serif;
  cursor: pointer;
  width: 280px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  border: 4px solid #f63630;
  
}
.Login-Form button:hover{
  transform: scale(1.1);
}
.Login-Form p{
  font-size: 10px;
  color: white;
}
.Link{
  color: #f63630;
  text-decoration: none;
  
}

@media only screen and (max-width:900px){
  .Login-Img{
    position: relative;
    align-self: center;
    max-height: 450px;
    margin: 0;
    overflow: hidden;
  }
  .Login-Form_Wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

}



